.services {
    min-height: 100vh;
    background: linear-gradient(180deg, #0d0d2b, #1a1a4e);
    color: #f0f0ff;
    padding: 80px 30px 40px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    box-sizing: border-box;
}

.services-header {
    text-align: center;
    max-width: 800px;
    margin: 0 auto 40px;
}

    .services-header h1 {
        font-size: 2.5rem;
        margin-bottom: 15px;
        color: #00d4ff;
    }

    .services-header p {
        font-size: 1.1rem;
        opacity: 0.8;
    }

.solutions-column {
    max-width: 800px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.solution-item {
    background: rgba(255, 255, 255, 0.05);
    padding: 20px;
    border-radius: 10px;
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

    .solution-item.animate-in {
        opacity: 1;
        transform: translateY(0);
    }

.solution-image {
    width: 100%;
    height: 400px; /* از 300px به 350px */
    object-fit: cover;
    border-radius: 10px;
    margin-bottom: 20px;
    transition: transform 0.3s ease;
}

.solution-item h3 {
    font-size: 1.6rem;
    margin-bottom: 15px;
    color: #e0e0ff;
}

.solution-item p {
    font-size: 1rem;
    margin-bottom: 10px;
    opacity: 0.8;
}

.detailed-text {
    font-size: 0.95rem;
    line-height: 1.6;
    opacity: 0.7;
}

@media (max-width: 768px) {
    .solution-image {
        height: 350px; /* از 200px به 250px */
    }


    .services-header h1 {
        font-size: 2rem;
    }

    .solution-image {
        height: 200px;
    }
}
