.about {
    min-height: 100vh;
    background: linear-gradient(180deg, #0d0d2b, #1a1a4e);
    color: #f0f0ff;
    padding: 80px 30px 40px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    box-sizing: border-box;
}

.about-header {
    text-align: center;
    max-width: 800px;
    margin: 0 auto 40px;
}

    .about-header h1 {
        font-size: 2.5rem;
        margin-bottom: 15px;
        color: #00d4ff;
    }

    .about-header p {
        font-size: 1.1rem;
        opacity: 0.8;
    }

.about-mission {
    text-align: center;
    max-width: 800px;
    margin: 0 auto 60px;
}

    .about-mission h2 {
        font-size: 1.8rem;
        margin-bottom: 15px;
        color: #00d4ff;
    }

    .about-mission p {
        font-size: 1rem;
        opacity: 0.8;
    }

.experts {
    max-width: 1200px;
    margin: 0 auto;
}

    .experts h2 {
        font-size: 1.8rem;
        text-align: center;
        margin-bottom: 20px;
        color: #00d4ff;
    }

.scroll-container {
    position: relative;
    padding: 0 50px;
}

.experts-grid {
    display: flex;
    overflow-x: auto;
    scroll-snap-type: x mandatory;
    gap: 20px;
    padding-bottom: 10px;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

    .experts-grid::-webkit-scrollbar {
        display: none;
    }

.expert-item {
    flex: 0 0 300px;
    background: rgba(255, 255, 255, 0.05);
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    scroll-snap-align: start;
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

    .expert-item.animate-in {
        opacity: 1;
        transform: translateY(0);
    }

.expert-image {
    width: 100%;
    height: 150px;
    object-fit: cover;
    border-radius: 10px;
    margin-bottom: 15px;
}

.expert-item h3 {
    font-size: 1.4rem;
    margin-bottom: 10px;
    color: #e0e0ff;
}

.expert-contact {
    font-size: 0.9rem;
    opacity: 0.8;
    margin: 5px 0;
}

.scroll-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 212, 255, 0.5);
    border: none;
    color: #fff;
    font-size: 1.5rem;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background 0.3s ease;
}

    .scroll-button:hover {
        background: rgba(0, 212, 255, 0.8);
    }

    .scroll-button.left {
        left: 0;
    }

    .scroll-button.right {
        right: 0;
    }

.about-item {
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

    .about-item.animate-in {
        opacity: 1;
        transform: translateY(0);
    }

@media (max-width: 768px) {
    .about {
        padding: 60px 20px 40px;
    }

    .about-header h1 {
        font-size: 2rem;
    }

    .about-mission h2 {
        font-size: 1.6rem;
    }

    .experts-grid {
        gap: 15px;
    }

    .expert-item {
        flex: 0 0 250px;
    }

    .scroll-button {
        width: 35px;
        height: 35px;
        font-size: 1.2rem;
    }
}
