.footer {
    background: linear-gradient(90deg, rgba(10, 10, 50, 0.95), rgba(30, 30, 80, 0.95));
    color: #e0e0ff;
    padding: 40px 30px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    box-sizing: border-box;
    margin-top: 0;
}

.footer-container {
    display: flex;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;
    gap: 20px;
}

.footer-column {
    flex: 1;
    min-width: 200px;
}

.footer-brand h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: #00d4ff;
}

.footer-brand p {
    font-size: 0.9rem;
    opacity: 0.8;
}

.footer-links h4,
.footer-social h4 {
    font-size: 1.2rem;
    margin-bottom: 15px;
    color: #00d4ff;
}

.footer-links ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.footer-links li {
    margin: 8px 0;
}

.footer-links a {
    color: #e0e0ff;
    text-decoration: none;
    font-size: 1rem;
    transition: color 0.3s ease;
}

    .footer-links a:hover {
        color: #00d4ff;
    }

.footer-social .social-icons {
    display: flex;
    gap: 15px;
}

.social-icon {
    width: 30px;
    height: 30px;
    fill: #e0e0ff;
    transition: fill 0.3s ease, transform 0.3s ease;
}

    .social-icon:hover {
        fill: #00d4ff;
        transform: scale(1.2);
    }

@media (max-width: 768px) {
    .footer-container {
        flex-direction: column;
        align-items: flex-start;
    }

    .footer-column {
        width: 100%;
        margin-bottom: 20px;
    }

    .footer-social .social-icons {
        justify-content: flex-start;
    }
}
