.home {
    min-height: 100vh;
    background: linear-gradient(180deg, #0d0d2b, #1a1a4e);
    color: #f0f0ff;
    overflow-x: hidden;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    box-sizing: border-box;
}

.hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 90vh;
    text-align: center;
    padding: 0 20px;
    background: linear-gradient(135deg, rgba(0, 225, 255, 0.15), rgba(123, 44, 191, 0.15));
    position: relative;
    overflow: hidden;
}

    .hero::before {
        content: '';
        position: absolute;
        top: -30%;
        left: -30%;
        width: 160%;
        height: 160%;
        background: radial-gradient(circle, rgba(0, 225, 255, 0.25) 5%, transparent 60%);
        animation: glow 12s infinite ease-in-out;
        z-index: -1;
    }

    .hero h1 {
        font-size: 4.5rem;
        font-weight: 900;
        margin-bottom: 20px;
        background: linear-gradient(90deg, #00e1ff, #7b2cbf);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        text-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
        animation: slideIn 1s ease-out;
    }

    .hero p {
        font-size: 1.5rem;
        max-width: 750px;
        margin-bottom: 40px;
        line-height: 1.8;
        color: #d0d0ff;
        animation: slideIn 1.2s ease-out;
    }

.cta-container {
    display: flex;
    gap: 25px;
    justify-content: center;
    flex-wrap: wrap;
}

.cta-button {
    padding: 14px 40px;
    background: linear-gradient(90deg, #00e1ff, #7b2cbf);
    color: #0d0d2b;
    font-size: 1.2rem;
    font-weight: 700;
    text-decoration: none;
    border-radius: 50px;
    transition: all 0.4s ease;
    box-shadow: 0 5px 15px rgba(0, 225, 255, 0.5);
    animation: slideIn 1.4s ease-out;
}

    .cta-button.secondary {
        background: transparent;
        border: 2px solid #00e1ff;
        color: #00e1ff;
        box-shadow: none;
    }

    .cta-button:hover {
        transform: translateY(-5px) scale(1.05);
        box-shadow: 0 8px 25px rgba(0, 225, 255, 0.7);
        background: linear-gradient(90deg, #7b2cbf, #00e1ff);
    }

    .cta-button.secondary:hover {
        background: linear-gradient(90deg, #00e1ff, #7b2cbf);
        color: #0d0d2b;
        border-color: transparent;
    }

.services-preview {
    padding: 100px 20px;
    text-align: center;
}

    .services-preview h2 {
        font-size: 3rem;
        margin-bottom: 50px;
        color: #00e1ff;
        text-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
    }

.services-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 40px;
    max-width: 1400px;
    margin: 0 auto;
}

.service-item {
    background: rgba(20, 20, 60, 0.85);
    padding: 40px;
    border-radius: 20px;
    transition: transform 0.4s ease, box-shadow 0.4s ease;
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.2);
}

    .service-item:hover {
        transform: translateY(-12px);
        box-shadow: 0 12px 30px rgba(0, 225, 255, 0.4);
    }

.service-icon {
    font-size: 5rem;
    margin-bottom: 25px;
    color: #7b2cbf;
    transition: transform 0.3s ease;
}

.service-item:hover .service-icon {
    transform: scale(1.15);
}

.service-item h3 {
    font-size: 2rem;
    margin-bottom: 20px;
    color: #f0f0ff;
}

.service-item p {
    font-size: 1.1rem;
    line-height: 1.6;
    color: #c0c0ff;
}

.solutions, .experts {
    padding: 100px 20px;
    text-align: center;
}

    .solutions h2, .experts h2 {
        font-size: 3rem;
        margin-bottom: 50px;
        color: #00e1ff;
        text-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
    }

.scroll-container {
    position: relative;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 60px;
}

.solutions-grid, .experts-grid {
    display: flex;
    gap: 15px; /* فاصله کمتر برای جا شدن سه کارت */
    overflow-x: auto;
    scroll-snap-type: x mandatory;
    padding: 20px 0;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

    .solutions-grid::-webkit-scrollbar, .experts-grid::-webkit-scrollbar {
        display: none;
    }

.solution-item, .expert-item {
    flex: 0 0 calc(29.6% - 10px); /* عرض کمتر برای جا شدن */
    background: rgba(20, 20, 60, 0.85);
    padding: 20px;
    border-radius: 20px;
    scroll-snap-align: start;
    transition: transform 0.4s ease, box-shadow 0.4s ease;
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.2);
}

    .solution-item:hover, .expert-item:hover {
        transform: translateY(-10px);
        box-shadow: 0 12px 30px rgba(0, 225, 255, 0.4);
    }

.solution-image {
    width: 100%;
    height: 280px; /* ارتفاع بیشتر برای Solutions */
    object-fit: cover;
    border-radius: 15px;
    margin-bottom: 20px;
    transition: transform 0.3s ease;
}
.expert-image {
    width: 100%;
    height: 200px; /* از 150px به 200px */
    object-fit: cover;
    border-radius: 10px;
    margin-bottom: 15px;
}

.solution-item:hover .solution-image, .expert-item:hover .expert-image {
    transform: scale(1.05);
}


.solution-item {
    background: rgba(255, 255, 255, 0.05);
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 0.6s ease-out, transform 0.6s ease-out, background 0.3s ease;
    text-decoration: none;
    color: inherit;
    display: block;
}

    .solution-item:hover {
        background: rgba(255, 255, 255, 0.1);
        cursor: pointer;
    }


.solution-item h3, .expert-item h3 {
    font-size: 1.8rem;
    margin-bottom: 15px;
    color: #f0f0ff;
}

.solution-item p {
    font-size: 1rem;
    line-height: 1.6;
    color: #c0c0ff;
}

.expert-contact {
    font-size: 0.95rem;
    color: #c0c0ff;
    margin: 8px 0;
}

.scroll-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: linear-gradient(90deg, #00e1ff, #7b2cbf);
    color: #0d0d2b;
    border: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    font-size: 1.5rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 4px 12px rgba(0, 225, 255, 0.5);
    transition: all 0.3s ease;
    z-index: 10;
}

    .scroll-button:hover {
        transform: translateY(-50%) scale(1.1);
        box-shadow: 0 6px 18px rgba(0, 225, 255, 0.7);
    }

    .scroll-button.left {
        left: 5px;
    }

    .scroll-button.right {
        right: 5px;
    }

.mission {
    padding: 100px 20px;
    text-align: center;
}

    .mission h2 {
        font-size: 3rem;
        margin-bottom: 30px;
        color: #00e1ff;
        text-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
    }

    .mission p {
        font-size: 1.3rem;
        max-width: 800px;
        margin: 0 auto;
        line-height: 1.7;
        color: #c0c0ff;
    }

.contact-us {
    padding: 100px 20px;
    text-align: center;
}

    .contact-us h2 {
        font-size: 3rem;
        margin-bottom: 40px;
        color: #00e1ff;
        text-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
    }

.contact-container {
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
    justify-content: center;
    align-items: center;
    max-width: 1000px;
    margin: 0 auto;
}

.contact-image {
    width: 100%;
    max-width: 400px;
    height: 300px;
    object-fit: cover;
    border-radius: 20px;
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease;
}

    .contact-image:hover {
        transform: scale(1.05);
    }

.contact-info {
    text-align: left;
    max-width: 400px;
}

    .contact-info h3 {
        font-size: 2rem;
        margin-bottom: 20px;
        color: #f0f0ff;
    }

    .contact-info p {
        font-size: 1.1rem;
        margin: 10px 0;
        color: #c0c0ff;
    }

    .contact-info a {
        color: #00e1ff;
        text-decoration: none;
        transition: color 0.3s ease;
    }

        .contact-info a:hover {
            color: #7b2cbf;
        }

@keyframes slideIn {
    from {
        opacity: 0;
        transform: translateY(50px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes glow {
    0% {
        transform: scale(1);
        opacity: 0.4;
    }

    50% {
        transform: scale(1.3);
        opacity: 0.2;
    }

    100% {
        transform: scale(1);
        opacity: 0.4;
    }
}

@media (max-width: 768px) {
    .hero h1 {
        font-size: 3rem;
    }

    .hero p {
        font-size: 1.3rem;
    }

    .cta-container {
        flex-direction: column;
        gap: 20px;
    }

    .cta-button {
        padding: 12px 35px;
        font-size: 1.1rem;
        width: 100%;
        max-width: 280px;
    }

    .services-preview, .solutions, .experts, .mission, .contact-us {
        padding: 60px 15px;
    }

        .services-preview h2, .solutions h2, .experts h2, .mission h2, .contact-us h2 {
            font-size: 2.2rem;
        }

    .service-icon {
        font-size: 4rem;
    }

    .scroll-container {
        padding: 0;
    }

    .solutions-grid, .experts-grid {
        flex-direction: column;
        overflow-x: hidden;
        overflow-y: auto;
        scroll-snap-type: none;
        gap: 25px;
    }

    .solution-item, .expert-item {
        flex: none;
        width: 100%;
        max-width: 400px;
        margin: 0 auto;
    }

    .solution-image {
        height: 220px; /* ارتفاع کمتر تو موبایل */
    }

    .scroll-button {
        display: none;
    }

    .contact-container {
        flex-direction: column;
        gap: 30px;
    }

    .contact-image {
        max-width: 100%;
        height: 250px;
    }

    .contact-info {
        text-align: center;
    }
}


/* انیمیشن برای کارت‌ها */
.solution-item,
.expert-item {
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 0.9s ease-out, transform 0.6s ease-out;
}

    .solution-item.animate-in,
    .expert-item.animate-in {
        opacity: 1;
        transform: translateY(0);
    }